/* -- tags */
button.button {
  background-color: transparent;
  border-radius: 0.2rem;
  font-size: 1.1rem;
  font-weight: bold;
  padding: var(--unit) calc(var(--unit) * 1.5);
}

button.button.contrast {
  border: solid 1px var(--color-text);
  color: var(--color-text);
}

button.button.contrast:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

header {
  padding-top: calc(var(--unit) * 2);
  padding-bottom: calc(var(--unit) * 2);
}

header .logo {
  font-size: 2.5rem;
}

header nav {
  align-self: center;
  flex: 1;
  margin-left: var(--unit);
  /* justify-content: flex-end; */
}

header nav a {
  color: var(--color-text-light);
  margin-left: calc(var(--offset) / 2);
  text-decoration: none;
}

header nav a:hover {
  color: var(--color-text);
}

header nav a > strong {
  background-color: var(--color-accent);
  border-radius: var(--border-radius);
  color: var(--color-primary);
  font-size: 0.8rem;
  padding: 0.2rem 0.4rem;
}

header nav a:hover > strong {
  color: var(--color-text);
}

section.text {
  background-color: var(--color-text);
  color: var(--color-text-contrast);
}

section.expanded {
  padding-bottom: calc(var(--offset) * 2);
  padding-top: calc(var(--offset) * 2);
}

nav.learn {
  background-color: var(--color-base-light);
  border-radius: var(--border-radius);
  max-width: 300px;
  padding: calc(var(--unit) * 2);
  position: sticky;
  top: calc(var(--offset) * 1);
}

nav.learn > a {
  color: var(--color-text-light);
  line-height: 2rem;
  display: block;
  text-decoration: none;
}

nav.learn > a:hover {
  color: var(--color-text-contrast);
}

/* -- classNames */
.row {
  align-items: baseline;
  display: flex;
  flex-direction: row;
}

.currency {
  font-family: var(--font-family-currency);
  font-size: var(--font-size-L);
  font-weight: bold;
}

.content {
  display: flex;
  margin: 0 auto;
  max-width: 1368px;
  padding-left: var(--offset);
  padding-right: var(--offset);
}

.content .col {
  flex: 1;
  /* background-color: tomato; */
}

.content .col:first-child {
  padding-right: var(--offset);
  flex: 2;
}

.content .col + .col:last-child {
  padding-left: var(--offset);
}

.info {
  color: var(--color-text-light);
  margin: 0 calc(var(--unit) / 2);
}

@media only screen and (max-width: 1024px) {
  .content {
    flex-direction: column;
  }

  .content .col {
    padding-right: 0;
    margin-bottom: calc(var(--offset) * 2);
  }

  .content .col:first-child {
    padding-right: 0;
    max-width: 100vw;
  }

  .content .col + .col:last-child {
    padding-left: 0;
  }

  .row {
    /* flex-direction: column;
    width: 100%; */
    background-color: blue;
  }
}
