@font-face {
  font-family: 'Content';
  src: url('../assets/fonts/Circular-SP-Book.woff2') format('woff2'),
    url('../assets/fonts/Circular-SP-Book.ttf') format('ttf');
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
}

@font-face {
  font-family: 'Content';
  src: url('../assets/fonts/Circular-SP-Bold.woff2') format('woff2'),
    url('../assets/fonts/Circular-SP-Bold.ttf') format('ttf');
  font-weight: 800;
  font-style: normal;
  font-stretch: normal;
}

@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@700&display=swap');

