:root {
  /* sizing */
  --unit: 1rem;
  --offset: 4vw;
  --border-radius: 0.4rem;
  /* colors */
  --color-primary: #16161a;
  --color-accent: #ffc491;
  --color-base: #2d2d32;
  --color-base-light: #eee;
  --color-text: #fff;
  --color-text-light: #999999;
  --color-text-contrast: #000;
  /* fonts */
  --font-family: 'Content';
  --font-family-currency: 'Content';
  --font-family-currency: 'IBM Plex Sans', sans-serif;
  --font-line-height: 150%;
  --font-size-default: 1rem;
  --font-size-regular: 1.3rem;
  --font-size-S: 0.8rem;
  --font-size-XL: 3rem;
  --font-size-L: 2.5rem;
}

@media only screen and (max-width: 480px) {
  :root {
    --font-size-regular: 1.15rem;
    --font-size-XL: 2.5rem;
    --font-size-L: 2rem;
  }
}
